.yumaBorder {
  position: relative;
  /* z-index: 100; */
  padding: 0 !important;
}


.yumaBorder::before {
  content: '';
  position: absolute;
  border: 1px solid #d9ab73;
  left: 0;
  right: 0;
  -webkit-transform: scale(0.97,0.94);
          transform: scale(0.97,0.94);
  width: 100%;
  height: 100%;
  /* pointer-events: none; */
  
}

.yumaBorder::after {
  content: '';
  position: absolute;
  border: 1px solid #d9ab73;
  left: 0;
  top: 0;
  right: 0;
  -webkit-transform:scale(0.95,0.98);
          transform:scale(0.95,0.98);
  width: 100%;
  height: 100%;
  /* pointer-events: none; */
}

