.faqButtons button {
  flex-grow: 1;
  cursor: pointer;
  position: relative;
  font-size: 30px;
  padding: calc(1rem/ 1.125) 1rem 1rem;
  border: .125rem solid #e2b987;
  color: #d9ab73;
  background-color: black;
  font-family: "Crimson Text Pro", serif;
  outline: none !important;
  transition: 250ms, cubic-bezier(0.215, 0.61, 0.355, 1);        
}

.faqButtons button + button {
  border-right: solid #e2b987;
  margin-right: calc(.125rem * -1);
}

.faqButtons button:hover , 
.faqButtons button:focus {
  flex-grow: 2;
  color: black;
  outline: none;
  text-shadow: none;
  background-color: #d9ab73;
}

.faqButtons button:focus {
  outline: .125rem dashed #d9ab73;
  outline-offset: calc(.125rem * -3);
}

.faqButtons:hover
button:focus:not(:hover) {
  flex-grow: 1;
  color: black;
  background-color: #d9ab73;
}

.faqButtons button:active {
  -webkit-transform: translateY(.125rem);
          transform: translateY(.125rem);
  color: black;
  background-color: #d9ab73;
}
  
